import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: "/",
    name: "Home",
    component: () =>
      import(/* webpackChunkName: "about" */ "@/views/Home.vue"),    
  },
  {
    path: "/acerca-de",
    name: "About",
    component: () => import(/* webpackChunkName: "about" */ "@/views/About.vue"),
    meta: {
      breadcrumb: {
        text: 'Acerca de',
        link: '/acerca-de'
      }
    }
  },
  // POLES ROUTES
  {
    path: "/destinos/:keyword",
    name: "SinglePole",
    component: () => import("@/views/Pole.vue"),
    meta: {
      breadcrumb ({keyword}) {
          return [{
            text: `Destino ${keyword}`,
            link: `/destinos/${keyword}`
          }]     
      },
    }
  },
  {
    path: "/destinos/:poleKey/:productKey",
    name: "Products",
    component: () => import("@/views/Product.vue"),
    meta: {
      breadcrumb ({poleKey, productKey}) {
          return [
            {
              text: `Destino ${poleKey}`,
              link: `/destinos/${poleKey}`
            },
            {
              text: `Producto ${productKey}`,
              link: `/destinos/${poleKey}/${productKey}`
            },

        ]     
      },
    }
  },

  // PRODUCTS
  {
    path: "/destinos/:poleKey/:productKey/:keyword",
    name: "HotelDetail",
    component: () => import("@/views/Products/HotelDetail.vue"),
    meta: {
      breadcrumb ({poleKey, productKey, keyword}) {
          return [
            {
              text: `Destino ${poleKey}`,
              link: `/destinos/${poleKey}`
            },
            {
              text: `Producto ${productKey}`,
              link: `/destinos/${poleKey}/${productKey}`
            },
            {
              text: `${keyword}`,
              link: `/destinos/${poleKey}/${productKey}/${keyword}`
            },
        ]     
      },
    }
  },
  
  // SEARCH-RESULT
  {
    path: "/resultado-de-busqueda",
    name: "SearchResult",
    component: () => import("@/views/SearchResult.vue"),
    meta: {
      breadcrumb: {
        text: 'Resultados',
        link: '/resultado-de-busqueda'
      }
    }
  },

  // TERM & POLICY
  {
    path: "/terminos-politicas",
    name: "TermsAndPolicy",
    component: () => import('@/views/Policy.vue'),
    meta: {
      breadcrumb: {
        text: 'Política de privacidad',
        link: '/terminos-politicas'
      }
    }
  },

  // CONTACT US
  {
    path: "/contactenos",
    name: "Contact",
    component: () => import("@/views/Contacto.vue"),
    meta: {
      breadcrumb: {
        text: 'Contactenos',
        link: '/contactenos'
      }
    }
  },

  // SEND REQUEST
  {
    path: "/envio-de-solicitud",
    name: "SendRequest",
    component: () => import("@/views/Request.vue"),
    meta: {
      breadcrumb: {
        text: 'Solicitud',
        link: '/envio-de-solicitud'
      }
    }
  },

  // CLIENT SUBSCRIPTION
  {
    path: "/usuario/suscripcion",
    name: "NewClient",    
    component: () => import("@/views/Clients/NewClient.vue"),   
    meta: {
      breadcrumb: {
        text: 'Suscripcion',
        link: '/usuario/suscripcion'
      }
    }   
  },
  {
    path: "/usuario/olvide-mi-clave",
    name: "ForgotPassword",
    component: () => import("@/views/Clients/ForgotPassword.vue"),
    meta: {
      breadcrumb: {
        text: 'Clave olvidada',
        link: '/suario/olvide-mi-clave'
      }
    }
  },  
  {
    path: "/usuario/olvide-mi-clave/:token",
    name: "ResetPassword",
    component: () => import("@/views/Clients/ForgotPassword.vue"),
    // meta: {
    //   breadcrumb: {
    //     text: 'Solicitud',
    //     link: '/usuario/olvide-mi-clave/:token'
    //   }
    // }
  }, 
  {
    path: "/verificacion-de-cuenta/:token",
    name: "VerifyUser",
    component: () => import("@/views/Clients/VerifyUser.vue"),
    // meta: {
    //   breadcrumb: {
    //     text: 'Solicitud',
    //     link: '/envio-de-solicitud'
    //   }
    // }
  },
  
  // Admin
  {
    path: "/admin",
    name: "Admin",        
    component: () => 
      import("@/views/Admin/Admin.vue")
  },
  {
    path: "/admin/:CrudKey",
    name: "CrudKey",
    component: () =>
      import("@/views/Admin/Admin.vue"),    
  },  
  {
    path: "/404",
    name: "NotFound",
    component: () =>
      import("@/views/404.vue"),    
  },  

  // 404
  // {
  //   // path: '/:catchAll()',
  //   path: '*',
  //   redirect: '/404'
  // }
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
